import React from 'react'
import { Formik, Field } from 'formik'
import { navigate } from 'gatsby-link'
import validationSchema from './validationSchema'

const encode = (data) => {
  return Object.keys(data)
    .map(key => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&')
}

const ContactForm = () => {
  return (
    <Formik
      initialValues={{ name: '', email: '', message: '' }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        fetch("/?no-cache=1", {                                 //eslint-disable-line
          method: 'POST',
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
          body: encode({
            'form-name': 'contact',
            ...values,
          }),
        })
          .then(() => {
            navigate('/contact/success')
            setSubmitting(false)
          })
          .catch(error => {
            console.log(error)
            alert("Error: Please Try Again!");                            //eslint-disable-line
            setSubmitting(false)
          })
      }}
      render={({
        errors,
        touched,
        isSubmitting,
        handleSubmit,
        handleReset,
      }) => (


        <form
          name='contact'
          onSubmit={handleSubmit}
          onReset={handleReset}
          data-netlify='true'
          data-netlify-honeypot='bot-field'
          className="w-full max-w-md"
          >

          <div class="flex items-center border-b border-b-2 border-teal-500 py-2">
            <Field className='appearance-none bg-transparent border-none w-full text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none' type='text' placeholder='Full Name' name='name' id='name' />
            {touched.name && errors.name && <small className='text-red-600'>{errors.name}</small>}
          </div>
          <div class="flex items-center border-b border-b-2 border-teal-500 py-2">
            <Field className='appearance-none bg-transparent border-none w-full text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none' type='email' placeholder='Email' name='email' id='email' />
            {touched.email && errors.email && <small className='text-red-600'>{errors.email}</small>}
          </div>
          <div class="flex items-center border-b border-b-2 border-teal-500 py-2">
            <Field className='appearance-none bg-transparent border-none w-full text-gray-700 mr-3 py-1 px-2 leading-tight focus:outline-none' component='textarea' placeholder='Message' name='message' id='message' rows='6' />
            {touched.message && errors.message && <small className='text-red-600'>{errors.message}</small>}
          </div>

          <button className='mx-2 my-4 shadow bg-purple-500 hover:bg-purple-400 focus:shadow-outline focus:outline-none text-white font-bold py-2 px-4 rounded' type='submit' disabled={isSubmitting}>Submit</button>
        </form>
      )}
    />
  )
}

export { ContactForm }
